<template>
  <div class="home">
    <Sidebar></Sidebar>
    <div class="home_calass">
        <div class="home_main">
          <div class="data_home">Ma’lumotlarni kiritng</div>
            <div class="home_forms">
              <form @submit.prevent="postDiploma()">
                <div class="form_home">
                  <div>
                    <model-select :options="options"
                                  v-model="item"
                                  placeholder="F.I.O">
                    </model-select>
                  </div>
                  <div>
                    <model-select :options="options2"
                                  v-model="item2"
                                  placeholder="Diplom kod">
                    </model-select>
                  </div>
                  <div>
                    <button>
                      <span v-if="!spin">Yaratish</span>
                      <b-spinner variant="white" v-if="spin" ></b-spinner>
                    </button>
                  </div>
                </div>
              </form>
            </div>
        </div>
    </div>
    <div class="diplom_tables">
        <div class="table">
            <table>
              <tr>
                <th>
                  №
                </th>
                <th>
                  FIO
                </th>
                <th>
                  Tadbir nomi
                </th>
                <th>
                  Tadbir sanasi
                </th>
              </tr>
              <tr v-for="(item,index) in data_diploma.results" :key="index+'AB'">
                <td>
                  {{item.unique_code}}
                </td>
                <td>
                  {{item.owner_first_name}}
                </td>
                <td>
                  {{item.sport_type[0].title}}
                </td>
                <td>
                  {{item.date}}
                </td>
              </tr>
            </table>
        </div>
      <div class="pagenations">
          <div>
            <button @click="prev()">Олдинги</button>
          </div>
          <div>
            Сахифа {{pages}} - {{data_diploma.total_pages}}
          </div>
        <div>
          <button @click="next()">Кейинги</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { ModelSelect } from 'vue-search-select'
import {data} from '@/users/user.json'
import Sidebar from "../components/Sidebar";
export default {
  data () {
    return {
      spin:false,
      options: [
      ],
      item:'',
      options2: [
      ],
      item2: '',
      options_data:[],
      data_diploma:[],
      pages:1,
    }
  },
  mounted() {
    data.rows.forEach(el=>{

      this.options.push(
          {
            "text":el.fullname,
            "value":el.fullname
          }
      )

    })
    this.base_url.get('diplomas?page='+this.pages)
    .then(res=>{

      this.data_diploma=res.data
      res.data.results.forEach(el=>{
          this.options2.push({
            "text":el.unique_code,
            "value":el
          })
      })

    })
  },
  methods: {
    next(){
      this.pages++
      this.base_url.get('diplomas?page='+this.pages)
          .then(res=>{

            this.data_diploma=res.data
            res.data.results.forEach(el=>{
              this.options2.push({
                "text":el.unique_code,
                "value":el
              })
            })

          })
    },
    prev(){
      if(this.pages>0){
        this.pages--
        this.base_url.get('diplomas?page='+this.pages)
            .then(res=>{

              this.data_diploma=res.data
              res.data.results.forEach(el=>{
                this.options2.push({
                  "text":el.unique_code,
                  "value":el
                })
              })

            })

      }
    },
    postDiploma(){
      this.spin=true
    this.base_url.patch(`/diplomas/${this.item2.value.id}/`,
          {
            "unique_code":this.item2.value.unique_code,
            "owner_first_name":this.item,
            "owner_last_name":this.item,
            "is_confirmed":true

          }, {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('token')}`,
            }
          })
      .then(()=>{
        this.spin=false
        this.base_url.get('diplomas')
            .then(res=>{
              this.item2=''
              res.data.results.forEach(el=>{
                if(el.is_confirmed==true){
                  this.data_diploma.push(el)
                }
              })
              this.$toast("Diplom briktirildi", {
                timeout: 2000,
                type:"success"
              });
            })

      })
      .catch(()=>{

        this.spin=false
        this.$toast("Diplom briktirishda xatolik bor", {
          timeout: 2000,
          type:"error"
        });
      })
    },

    reset () {
      this.item = {}
    },
    selectFromParentComponent1 () {
      // select option from parent component
      this.item = this.options[0]
    },
    reset2 () {
      this.item2 = ''
    },
    selectFromParentComponent2 () {
      // select option from parent component
      this.item2 = this.options2[0].value
    }
  },
  components: {
    Sidebar,
    ModelSelect
  }
}
</script>
